html {
  font-size: 16px;
  user-select: none;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 1rem;
  line-height: 2;
}

a {
  text-decoration: none;
}

img {
  max-inline-size: 100%;
  vertical-align: bottom;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.wrapper {
  max-inline-size: 75rem;
  inline-size: 92%;
  margin: 0 auto;
}

h1,
h2,
.head-white,
h3 {
  margin: 0;
  margin-block-end: 3.125rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  text-align: center;
  text-transform: uppercase;
}

h1 {
  font-size: 2.5rem;
}

h2, .head-white {
  font-size: 2.25rem;
  color: #5E311B;
}
h2 span, .head-white span {
  font-size: 1.125rem;
  color: #C39C63;
  display: block;
}
h2::after, .head-white::after {
  content: " ";
  display: block;
  inline-size: 6.25rem;
  block-size: 6.25rem;
  background-image: url(../img/cafe.svg);
  background-size: cover;
  margin: 0 auto;
}

.head-white {
  color: #ffffff;
}
.head-white span {
  color: #ffffff;
}
.head-white::after {
  display: none;
}

h3 {
  font-size: 2rem;
}

.header {
  padding-block: 3.125rem;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
.header::before {
  content: " ";
  position: absolute;
  inset-block-start: 0;
  inset-block-end: 0;
  inset-inline-start: 0;
  inset-inline-end: 0;
  background: #000000;
  opacity: 0.6;
}
@media (min-width: 768px) {
  .header {
    min-block-size: 700px;
    box-sizing: border-box;
  }
}
.header .header-content {
  position: relative;
}
.header .header-content .header-title {
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  margin-block-start: 3.125rem;
}
@media (min-width: 768px) {
  .header .header-content .header-title {
    margin-block-start: 6.25rem;
  }
}

@supports (background-image: url("../img/header_inicio.jpg")) {
