    background-image: url("../img/header_proceso.jpg");
  }
}
@supports (background-image: url("../img/header_proceso.webp")) {
  .header-proceso {
    background-image: url("../img/header_proceso.webp");
  }
}
@supports (background-image: url("../img/header_proceso.avif")) {
  .header-proceso {
    background-image: url("../img/header_proceso.avif");
  }
}

@supports (background-image: url("../img/header_menu.jpg")) {
  .header-menu {
    background-image: url("../img/header_menu.jpg");
  }
}
@supports (background-image: url("../img/header_menu.webp")) {
  .header-menu {
    background-image: url("../img/header_menu.webp");
  }
}
@supports (background-image: url("../img/header_menu.avif")) {
  .header-menu {
    background-image: url("../img/header_menu.avif");
  }
}

@supports (background-image: url("../img/header_galeria.jpg")) {
  .header-galeria {
    background-image: url("../img/header_galeria.jpg");
  }
}
