.bar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.bar .logo {
  inline-size: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bar .logo {
    inline-size: 8.5rem;
    margin: 0;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-block-start: 2rem;
}
.nav .nav-link {
  color: #ffffff;
  text-align: center;
  font-size: 1;
  text-transform: uppercase;
  font-weight: 700;
}
.nav .nav-link[aria-current=page] {
  color: #C39C63;
}
.nav .nav-link:hover {
  color: #C39C63;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    margin: 0;
  }
}

.contentMain {
  background-color: #ffffff;
  padding-inline: 1.25rem;
  padding-block: 3.125rem;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .contentMain {
    margin-block-start: -6.25rem;
    position: relative;
    padding: 6.25rem;
  }
}

.icons-list {
  margin-block: 3.125rem;
}
@media (min-width: 768px) {
  .icons-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3.125rem;
  }
}
.icons-list .icons-icon {
  inline-size: 7.5rem;
}
.icons-list .icons-text {
  font-family: "Bona Nova", serif;
  font-size: 2rem;
  color: #5E311B;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.25rem;
}
@media (min-width: 1024px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.25rem;
  }
}
.gallery .gallery-item .gallery-img {
  inline-size: 18.75rem;
  block-size: 18.75rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .gallery .gallery-item:nth-of-type(1) {
    grid-row: 1/3;
  }
  .gallery .gallery-item:nth-of-type(1) .gallery-img {
    block-size: 38.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(2) {
    grid-column: 2/4;
  }
  .gallery .gallery-item:nth-of-type(2) .gallery-img {
    inline-size: 38.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(4) {
    grid-row: 2/4;
  }
  .gallery .gallery-item:nth-of-type(4) .gallery-img {
    block-size: 38.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(5) {
    grid-column: 3/5;
  }
  .gallery .gallery-item:nth-of-type(5) .gallery-img {
    inline-size: 38.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(6) {
    grid-column: 3/5;
  }
  .gallery .gallery-item:nth-of-type(6) .gallery-img {
    inline-size: 38.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(7) {
    grid-row: 3/5;
  }
  .gallery .gallery-item:nth-of-type(7) .gallery-img {
    block-size: 38.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(8) {
    grid-column: 2/5;
    grid-row: 4;
  }
  .gallery .gallery-item:nth-of-type(8) .gallery-img {
    inline-size: 58.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(9) {
    grid-column: 1/3;
  }
  .gallery .gallery-item:nth-of-type(9) .gallery-img {
    inline-size: 38.75rem;
    object-position: center;
  }
  .gallery .gallery-item:nth-of-type(10) {
    grid-column: 3/-1;
  }
  .gallery .gallery-item:nth-of-type(10) .gallery-img {
    inline-size: 38.75rem;
    object-position: center;
  }
}

.ourMenu {
  background-color: #5E311B;
  padding: 9.375rem 1rem 12.5rem 1rem;
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg);
  background-repeat: no-repeat;
  background-position: center top 3.125rem, center bottom 6.25rem;
  background-size: 200%;
}
@media (min-width: 768px) {
  .ourMenu {
    padding: 9.375rem 0 15.5rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .ourMenu {
    background-size: 50%;
  }
}

@media (min-width: 768px) {
  .contentMenu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
  }
}
@media (min-width: 1024px) {
  .contentMenu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.menu:nth-of-type(1) {
  margin-block-end: 2rem;
}
.menu .title {
  color: #ffffff;
}
.menu .item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}
.menu .name {
  color: #ffffff;
  font-size: 1.375rem;
  margin: 0;
  margin-block-end: 1.25rem;
  line-height: 1.5;
}
.menu .description {
  display: block;
  color: #C39C63;
}
.menu .precio {
  color: #ffffff;
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 768px) {
  .menu:nth-of-type(1) {
    margin: 0;
  }
  .menu .precio {
    font-size: 2.5rem;
  }
}

.ourTestimony {
  padding-block: 6.25rem;
  padding-inline: 1.25rem;
  background: #ffffff;
  margin-block-start: -6.25rem;
  position: relative;
  box-sizing: border-box;
}
.ourTestimony .testimony {
  background: #ebe7de;
  padding-inline: 2.5rem;
  padding-block: 1.25rem;
  border-radius: 1rem;
  margin: 0 auto;
}
.ourTestimony .author {
  text-align: right;
  font-size: 1.25rem;
  font-family: "Bona Nova", serif;
  color: #5E311B;
}
.ourTestimony .author::before {
  content: "- ";
}
@media (min-width: 768px) {
  .ourTestimony .testimony {
    max-inline-size: 50%;
  }
  .ourTestimony .author {
    font-size: 1.5rem;
  }
}

.footer {
  background: #5E311B;
}
.footer .content {
  background: #ffffff;
  padding: 3.125rem;
  padding-inline: 2rem;
}
@media (min-width: 768px) {
  .footer .content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.25rem;
    padding: 3.125rem;
  }
}
.footer .reservation {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer .copyright {
  margin: 0;
  padding: 1.5rem;
  color: #ffffff;
  text-align: center;
}

.footer-box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-block-end: 3.125rem;
}
.footer-box .title {
  margin-block-end: 1rem;
  font-family: inherit;
  color: #C39C63;
  font-size: 1.5rem;
}
.footer-box .footer-text,
.footer-box .footer-button {
  color: #000000;
  font-size: 1.25rem;
  margin: 0;
}
.footer-box .footer-button {
  margin-block: 0.5rem;
  padding-inline: 1rem;
  padding-block: 0.765rem;
  background: #5E311B;
  color: #ffffff;
  border-radius: 1rem;
  cursor: pointer;
  transition: scale 0.3s;
}
.footer-box .footer-button:focus, .footer-box .footer-button:hover {
  scale: 0.9;
}
@media (min-width: 768px) {
  .footer-box {
    margin-block-end: 0;
  }
}

.content-nosotros .picture {
  margin-block-end: 2rem;
}
.content-nosotros .bloque {
  margin: 0;
  padding: 1rem;
  border-inline-start: 0.3125rem solid #5E311B;
  font-size: 1.25rem;
}
@media (min-width: 768px) {
  .content-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
    align-items: center;
  }
}
.content-nosotros .picture {
  margin-inline-end: 60px;
  margin-block-end: 0;
}
.content-nosotros .img {
  transform: rotate(-6deg);
  border: 1.25rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}

.content-process .process-coffe {
  margin-block-end: 3.125rem;
}
.content-process .process-coffe:last-of-type {
  margin-block-end: 0;
}
.content-process .description {
  margin-block-start: 0.625rem;
}
.content-process .title {
  text-align: start;
  color: #5E311B;
  display: inline-block;
  margin-block-end: 0;
  border-block-end: 0.3125rem solid #C39C63;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .content-process .process-coffe {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  .content-process .process-coffe > * {
    flex: 1;
  }
  .content-process .process-coffe:nth-of-type(even) .description {
    order: -1;
  }
  .content-process .description {
    margin-block-start: 0;
  }
}
@media (min-width: 1024px) {
  .content-process .process-coffe {
    gap: 2.5rem;
  }
  .content-process .title {
    font-size: 2rem;
  }
}

.content-menu::after {
  content: " ";
  display: block;
  inline-size: 100%;
  block-size: 12.5rem;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: contain;
  margin: auto;
}
@supports (background-image: url("../img/cafe.jpg")) {
  .content-menu::after {
    background-image: url("../img/cafe.jpg");
  }
}
@supports (background-image: url("../img/cafe.webp")) {
  .content-menu::after {
    background-image: url("../img/cafe.webp");
  }
}
@supports (background-image: url("../img/cafe.avif")) {
  .content-menu::after {
    background-image: url("../img/cafe.avif");
  }
}
@media (min-width: 480px) {
  .content-menu::after {
    block-size: 18.75rem;
  }
}
@media (min-width: 768px) {
  .content-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
  .content-menu::after {
    block-size: 100%;
  }
}
.content-menu .description {
  font-size: 1.25rem;
}

@media (min-width: 768px) {
  .content-contacto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
    background-repeat: no-repeat;
    background-position: right;
    background-size: 40%;
  }
  @supports (background-image: url("../img/granos_cafe.jpg")) {
    .content-contacto {
      background-image: url("../img/granos_cafe.jpg");
    }
  }
  @supports (background-image: url("../img/granos_cafe.webp")) {
    .content-contacto {
      background-image: url("../img/granos_cafe.webp");
    }
  }
  @supports (background-image: url("../img/granos_cafe.avif")) {
    .content-contacto {
      background-image: url("../img/granos_cafe.avif");
    }
  }
}

